import back from '@/components/back'
export default {
    components: {
        back
    },
    data(){
        return{
            list: [],
            show: false,
            model: '',
            index: 0,
            Patient: this.$store.state.patient,
        }
    },
    computed:{
        title(){
            if(!this.$route.query.fromList){
                return `${this.Patient.PatientName}的随访计划`
            }else{
                return `随访计划`
            }
            
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            if(this.$route.query.fromList){
                this.$get('PlatFormAPI/Followup/QueryTaskTemplet',{
                    PlanTempId: this.$route.query.id
                }).then(o => {
                    this.list = o
                })
            }
            else{
                this.$get('PlatFormAPI/Followup/QueryTaskRecord',{
                    PlanId: this.$route.query.id
                }).then(o => {
                    this.list = o
                })
            }
            
        },
        toShow(i){
            if(i.QuestionnaireUrl){
                this.$bridge.callhandler('createWebView',i.QuestionnaireUrl)
            }
            if(i.LinkURL){
                this.$bridge.callhandler('createWebView',i.LinkURL)
            }
        },
    }
}



